export const isJson = (str) => {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
};

export const isEmpty = (val) => {
    if (val === null || val === undefined) {
        return true;
    } else if (val.constructor.name === "Array") {
        return val.length === 0;
    } else if (typeof val === "object") {
        return Object.keys(val).length === 0 && val.constructor.name === "Object";
    } else if (typeof val === "string") {
        return val.length === 0;
    } else if (typeof val === "number") {
        return val === 0;
    } else if (!val) {
        return true;
    }
    return false;
};

export const shuffleArray = (array) => {
    let currentIndex = array.length;
    let randomIndex, temporaryValue;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }

    return array;
};

export const Cookie = {
    get: function (name) {
        name = name + "=";
        const ca = decodeURIComponent(document.cookie).split(";");
        for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) === " ") {
                c = c.substring(1);
            }
            if (c.indexOf(name) === 0) {
                return c.substring(name.length, c.length);
            }
        }
        return "";
    },

    set: function (name, value, exhrs = 1) {
        const d = new Date();
        d.setTime(d.getTime() + exhrs * 60 * 60 * 1000);
        document.cookie = name.concat("=", value, ";expires=", d.toUTCString(), "; path=/");
    },
};

export const LocalStorage = {
    get: (key, val = "") => {
        const data = window.localStorage.getItem(key);
        return (isJson(data) ? JSON.parse(data) : data) || val;
    },
    set: (key, val = "") => {
        if ((!isEmpty(val) && val.constructor.name === Array) || typeof val === "object") {
            val = JSON.stringify(val);
        }
        return window.localStorage.setItem(key, val) || val;
    },
    del: (key, val = "") => window.localStorage.removeItem(key) || val,
};
